import React, { Component } from 'react';

export class Period extends Component {

    constructor(props) {
        super(props);
        if (props.start === undefined) {
            console.error("no start date specified");
            return;
        }
        const options = { month: 'long',  year: 'numeric'};
        const dateFormat = new Intl.DateTimeFormat('en-UK', options)
        let startDate = new Date(props.start)
        let startString = dateFormat.format(startDate)
        let endString = "Present"
        if (props.end !== undefined) {
            let endDate = new Date(props.end)
            endString = dateFormat.format(endDate)
        }
        this.state = {
            startString: startString,
            endString: endString,
        }
    }

    render() {
        return (
            <div className="resume-date text-md-right">
                <span className="text-primary">{this.state.startString} - {this.state.endString}</span>
            </div>
        );
    }
}

export default Period;
