import React, { Component } from 'react';
import Linkify from 'linkify-react';

import resume from '../../../resume/resume.json'
import Period from './utils/Period';
import Chip from './utils/Chip';

export class Experience extends Component {

    render() {
        return (
            <section
                className="resume-section p-3 p-lg-5 d-flex justify-content-center"
                id="experience"
            >
                <div className="w-100">
                    <h2 className="mb-5">Experience</h2>
                    {resume.work.map((job) => {
                        return (
                            <>
                            <div className="resume-item d-flex flex-column flex-md-row justify-content-between mt-5">
                                <div className="resume-content">
                                    <h3 className="mb-0">{job.position}</h3>
                                    <div className="subheading mb-0">{job.name}</div>
                                </div>
                                <Period start={job.startDate} end={job.endDate} />
                            </div>
                            <div>
                            <div className="mb-3">
                            {job.keywords.slice(0, 10).map((keyword) => <Chip text={keyword} />)}
                            </div>
                            <p><ul>
                                {job.highlights.map((highlight) => <Linkify tagName="li">{highlight}</Linkify>)}
                            </ul></p>
                            </div>
                            </>
                        )
                    })}
                </div>
            </section>
        )
    }
}

export default Experience;
