import React, { Component } from 'react';

import resume from '../../../resume/resume.json'
import Period from './utils/Period';

export class Education extends Component {

    render() {
        return (
            <section
                className="resume-section p-3 p-lg-5 d-flex align-items-center"
                id="education"
            >
                <div className="w-100">
                    <h2 className="mb-5">Education</h2>
                    {resume.education.map((education) =>
                        <div className="resume-item d-flex flex-column flex-md-row justify-content-between mt-5">
                            <div className="resume-content">
                                <h3 className="mb-0">{education.studyType}</h3>
                                <div className="subheading mb-3">{education.institution}</div>
                                <p>GPA: {education.score}</p>
                                <ul>
                                    {education.courses.map((course) => <li>{course}</li>)}
                                </ul>
                            </div>
                            <div className="resume-date text-md-right">
                                <span className="text-primary">
                                    <Period start={education.startDate} end={education.endDate} />
                                </span>
                            </div>
                        </div>
                    )}
                </div>
            </section>
        );
    }
}

export default Education;
